.navigation{font-family:'PT Mono', monospace;width:100%}.navigation.fixed{position:fixed;z-index:1000;background-color:#002731 !important}.navigation .nav-link{color:white;position:relative;padding:1rem 1.5rem}.navigation .nav-link:focus{color:white}.navigation .nav-link:hover{color:#f5f5f5}.navigation .nav-link:hover::after{width:80%}.navigation .nav-link::after{position:absolute;top:40px;left:0;right:0;margin:auto;background-color:#00CED1;color:transparent;width:0%;content:".";height:2px;transition:all 0.5s}

.home-text{color:white;line-height:1.2;height:95vh}.home-text h5{font-family:'PT Mono', monospace}.home-text span span{position:relative;animation:move-text-color 0.75s forwards;bottom:-1em;opacity:0;font-family:'Montserrat', sans-serif;font-size:calc(40px + (70 - 40) * ((100vw - 300px) / (1600 - 300)));font-weight:700}@keyframes move-text-color{50%{bottom:0.2em;color:#4F9493}75%{bottom:0.1em;color:#00CED1}100%{bottom:0;opacity:1;color:white}}

.btn{margin-top:50px;margin-bottom:50px;width:250px;padding:0.75rem !important}.btn-one{position:relative}.btn-one span{transition:all 0.3s;font-weight:700;text-transform:uppercase;font-family:"Montserrat";color:white;position:relative;z-index:100}.btn-one::before{content:'';position:absolute;bottom:0;left:0;width:100%;height:100%;z-index:1;opacity:0;transition:all 0.3s;border-top-width:3px;border-bottom-width:3px;border-top-style:solid;border-bottom-style:solid;border-top-color:rgba(0,206,209,0.7);border-bottom-color:rgba(0,206,209,0.7);transform:scale(0.1, 1);border-radius:50px}.btn-one:hover span{letter-spacing:2px}.btn-one:hover::before{opacity:1;transform:scale(1, 1)}.btn-one::after{content:'';position:absolute;bottom:0;left:0;width:100%;height:100%;z-index:1;transition:all 0.3s;background-color:rgba(0,206,209,0.3)}.btn-one:hover::after{opacity:0;transform:scale(0.1, 1)}

.about-container{color:white;font-family:Montserrat;margin-top:80px;max-width:95%}.about-container .border{border:1px rgba(255,255,255,0.5) solid;padding:75px 50px 50px}

.underline--magical{background-image:linear-gradient(120deg, #00ced1 0%, #003a4a 100%);background-repeat:no-repeat;background-size:100% 0.2em;background-position:0 88%;transition:background-size 0.25s ease-in}.underline--magical:hover{background-size:100% 88%}.turquoise{color:#00ced1}.bold{font-weight:700}.list-circle{list-style:circle}.uppercase{text-transform:uppercase}.center-col{display:table;margin:auto}.white{color:white}h1.title{color:#e8e8e8;font-weight:700;text-transform:uppercase;font-size:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)))}

.project-container{margin-top:150px;text-align:end;color:white;font-family:Montserrat}.project-container .each-project{background-color:white;color:black;opacity:0.6;padding:3rem 3rem 2rem;text-align:left}.project-container .each-project .github-icon{transform:scale(2.5);margin-left:30px}.project-container .each-project .github-icon:hover{color:#008d8f}.project-container .each-project:hover{opacity:0.7}

.contact-container{margin-top:200px;margin-bottom:200px}.contact-container .contact-text{color:white;font-family:'Montserrat', sans-serif;font-size:20px;margin-bottom:25px}.contact-input input,.contact-input textarea{width:100%;background-color:#909da2}.contact-input input{height:40px;margin-bottom:30px}.contact-input textarea{height:120px}.contact-input :-ms-input-placeholder{color:black}.contact-input ::placeholder{color:black}

